import React, { useState } from "react";
import './cal.css';
function Calcuator(){
  const [result, setResult]=useState('');
  function numberHandle(e){
setResult(result.concat(e.target.value))

  }
 const equall =()=>{
  setResult(eval(result).toString())
  console.log(result)
 }
 function numberRemove() {
  let length=result.length-1
  let copy=result.slice(0,length)
  setResult(copy)
 }
 function numbersHandle(){
  setResult(eval(result).toString())
  let y =eval(result)
  let z= Number(y)
  let x=z/100 ;
  // console.log(typeof(z),z)
  // console.log(x,'x ')
 let a=x.toString()
  // console.log(typeof(x))
  // console.log(a)
setResult(a)
}
  return (
    <div className="outer">
    <div className="cal">
      <h1>Welcome to Aman's Calculator</h1>
<input type="text" id="answer" placeholder="0" value={result} />
<input type="button" className="button" value={'clear'} onClick={()=>setResult('')}/>
<input type="button" className="button"value={'%'}onClick={(e)=>{numbersHandle(e)}}/>
<input type="button" className="button"value={"remove"}onClick={()=>{numberRemove()}}/>
<input type="button" className="button"value={'/'}onClick={(e)=>{numberHandle(e)}}/>

<input type="button" className="button" value={'7'} onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'8'} onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'9'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button"className="button"  value={'*'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'4'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'5'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'6'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button"className="button"  value={'-'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'1'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button" value={'2'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button"className="button" value={'3'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button"className="button" value={'+'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button"value={'00'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button" className="button"value={'0'}onClick={(e)=>{numberHandle(e)}}/>
<input type="button"className="button" value={'.'}onClick={(e)=>{numberHandle(e)}}/>

<input type="button" className="button" value={'='}onClick={()=>equall()}/>



    </div></div>
  )
}export default Calcuator